import Studio from 'jsreport-studio'
import 'brace/mode/jade'
import shortid from 'shortid'
import save from './save.js'
import toolbar from './toolbar.js'

import Startup from './Startup.js'

Studio.workspaces = {}

Studio.workspaces.current = {}

const workspaceRegex = /\/studio\/workspace\/([^\/]+)\/([^\/]+)/
const playgroundRegex = /[\/]?playground\/([^\/]+)[\/]?([^\/]+)?/

const getQueryParameter = (name) => {
  var match = RegExp('[?&]' + name + '=([^&]*)').exec(window.location.search)
  return match && decodeURIComponent(match[1].replace(/\+/g, ' '))
}

const isEmbed = getQueryParameter('embed') != null

Studio.toolbarVisibilityResolver = (text) => {
  return text === 'Run'
}

Studio.addEditorComponent('Help', Startup)
Studio.addStartupPage('Help', 'Help', 'Get Started')

toolbar(isEmbed)
Studio.workspaces.save = save

Studio.readyListeners.push(async () => {
  if (isEmbed) {
    Studio.collapseLeft()
  }

  if (Studio.workspaces.current.default) {
    const entity = Studio.getEntityByShortid(Studio.workspaces.current.default, false)
    if (entity) {
      Studio.openTab({ _id: entity._id })
    }
  }
})

Studio.initializeListeners.push(async () => {
  const match = workspaceRegex.exec(window.location.pathname)

  if (match) {
    const response = await Studio.api.get(`/odata/workspaces?$filter=shortid eq '${match[1]}' and version eq ${parseInt(match[2])}`)

    Studio.workspaces.current = response.value.length ? response.value[0] : {
      shortid: match[1],
      version: parseInt(match[2])
    }
  } else {
    const oldMatch = playgroundRegex.exec(window.location.hash)

    if (oldMatch) {
      window.location = `/studio/workspace/${oldMatch[1]}` + (oldMatch[2] ? `/${oldMatch[2]}` : '/1')
    }

    Studio.workspaces.current.shortid = shortid.generate()
    Studio.workspaces.current.version = 1
    Studio.store.dispatch(Studio.editor.actions.updateHistory())
  }

  Studio.addApiHeaders({
    'workspace-shortid': Studio.workspaces.current.shortid,
    'workspace-version': Studio.workspaces.current.version
  })
})

Studio.locationResolver = (path, entity) => {
  return `/studio/workspace/${Studio.workspaces.current.shortid}/${Studio.workspaces.current.version}`
}

Studio.loadReferencesListeners.push(async (entitySet) => {
  const nameAttribute = Studio.entitySets[entitySet].nameAttribute

  let response = await Studio.api.get(`/odata/${entitySet}?$filter=workspaceVersion eq ${Studio.workspaces.current.version} and workspaceShortid eq '${Studio.workspaces.current.shortid}'&$select=${nameAttribute},shortid&$orderby=${nameAttribute}`)

  Studio.store.dispatch({
    type: Studio.entities.ActionTypes.LOAD_REFERENCES,
    entities: response.value,
    entitySet: entitySet
  })

  if (!isEmbed) {
    Studio.openTab({ key: 'Help', editorComponentKey: 'Help', title: 'Get Started' })
  }

  response.value.forEach((v) => Studio.openTab({ _id: v._id }))

  return 'block'
})

Studio.workspaces.setDefault = () => {
  if (Studio.workspaces.current.default === Studio.getActiveEntity().shortid) {
    Studio.workspaces.current.default = undefined
  } else {
    Studio.workspaces.current.default = Studio.getActiveEntity().shortid
  }
  Studio.workspaces.save()
}




